


























import Vue from 'vue';
import { View } from '../store/index'

export default Vue.extend({
  name: 'Nav',
  computed: {
    currentView() { return this.$store.state.currentView }
  },
  methods: {
    toggleMenu() {
      this.$root.$emit('toggleSidebar');
    },
    setView(view: View) {
      this.$store.commit('setView', view)
    },
  },
});
